//==================================================select=====================================================================
.custom-select {
  position: relative;
  border-radius: 9px;
  border: 1px solid $main-color;
  //width: 100%;
  min-width: 85px;
  padding-left: 10px;
  padding-right: 40px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.custom-select select {
  display: none; /*hide original SELECT element:*/
}

.select-selected {
  background-color: transparent;
  &:before {
    background-image: url("../img/ru.png");
    background-repeat: no-repeat;
    width: 17px;
    height: 12px;
    position: absolute;
    right: 24px;
    top: calc(50% - 7px);
    display: block;
    background-size: cover;
    content: '';
  }
  &[data-language="0"] {
    &:before {
      background-image: url("../img/ru.png");
      background-repeat: no-repeat;
      width: 17px;
      height: 12px;
      position: absolute;
      right: 20px;
      top: calc(50% - 7px);
      display: block;
      background-size: cover;
      content: '';
    }
  }
  &[data-language="1"] {
    &:before {
      background-image: url("../img/uk.png");
      background-repeat: no-repeat;
      width: 17px;
      height: 12px;
      position: absolute;
      right: 20px;
      top: calc(50% - 7px);
      display: block;
      background-size: cover;
      content: '';
    }
  }
  &[data-language="2"] {
    &:before {
      background-image: url("../img/ua.png");
      background-repeat: no-repeat;
      width: 17px;
      height: 12px;
      position: absolute;
      right: 20px;
      top: calc(50% - 7px);
      display: block;
      background-size: cover;
      content: '';
    }
  }
}

/*style the arrow inside the select element:*/
.select-selected:after {
  position: absolute;
  content: "";
  top: calc(50% - 2px);
  right: 10px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 4px 2.5px 0 2.5px;
  border-color: #ffffff transparent transparent transparent;
}

/*point the arrow upwards when the select box is open (active):*/
.select-selected.select-arrow-active:after {
  border-color: transparent transparent #fff transparent;
  top: 7px;
}

/*style the items (options), including the selected item:*/
.select-items div, .select-selected {
  color: #ffffff;
  //padding: 8px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  font-size: 14px;
}

/*style items (options):*/
.select-items {
  position: absolute;
  background-color: transparent;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
  border: 1px solid $main-color;
  border-radius: 9px;
  margin-top: 10px;
}

/*hide the items when the select box is closed:*/
.select-hide {
  display: none;
}

.select-items div:hover, .same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}

.select-items a {
  padding: 8px 10px;
  display: block;
}

.select-items {
  & > a:nth-child(1) {
    position: relative;
    &:after {
      background-image: url("../img/ru.png");
      background-repeat: no-repeat;
      width: 17px;
      height: 12px;
      position: absolute;
      right: 10px;
      top: calc(50% - 7px);
      display: block;
      background-size: cover;
      content: '';
    }
  }
  & > a:nth-child(2) {
    position: relative;
    &:after {
      background-image: url("../img/uk.png");
      background-repeat: no-repeat;
      width: 17px;
      height: 12px;
      position: absolute;
      right: 10px;
      top: calc(50% - 7px);
      display: block;
      background-size: cover;
      content: '';
    }
  }
  & > a:nth-child(3) {
    position: relative;
    &:after {
      background-image: url("../img/ua.png");
      background-repeat: no-repeat;
      width: 17px;
      height: 12px;
      position: absolute;
      right: 10px;
      top: calc(50% - 7px);
      display: block;
      background-size: cover;
      content: '';
    }
  }
}

