//==================================================flipster slider=====================================================================

.flipster--coverflow .flipster__item .flipster__item__content {
  transform: scale(0.8) rotateY(0deg);
  border-radius: 10px;
  overflow: hidden;
  opacity: 0.55;
}

.flipster--coverflow .flipster__item.flipster__item--current .flipster__item__content {
  transform: scale(1) rotateY(0deg);
  opacity: 1;
  border-radius: 10px;
  overflow: hidden;
}

.flipster__nav__link {
  font-size: 0px;
  height: 10px;
  width: 10px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 100px;
  &:hover {
